@import url("https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,100..900;1,100..900&family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Orbitron:wght@400..900&family=Parkinsans:wght@300..800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Rubik:ital,wght@0,300..900;1,300..900&family=Sour+Gummy:ital,wght@0,100..900;1,100..900&family=Staatliches&display=swap");
::after,
::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

span {
  color: #FFFFFF;
}

a {
  text-decoration: none;
  font-family: "Lato", sans-serif;
}

ul {
  margin-bottom: 0;
}

li {
  list-style: none;
}

h1 {
  font-family: "Rubik", Courier;
  font-weight: 600;
  font-size: 1.5rem;
  color: #42A5F5;
}

h2 {
  font-family: "Rubik", Courier, 300;
}

body {
  font-family: "Lato", sans-serif;
  background-color: rgb(233.55, 233.55, 233.55);
}

input {
  font-family: "Lato", sans-serif;
}

.main {
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
  transition: all 0.35s ease-in-out;
  background-color: #e7e7e7;
}

#sidebar {
  height: 98vh;
  width: 80px;
  min-width: 80px;
  z-index: 1000;
  transition: all 0.25s ease-in-out;
  background-color: rgb(103.5, 103.5, 103.5);
  display: flex;
  flex-direction: column;
  position: fixed;
  border-radius: 3px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
}

#sidebar.expand {
  width: 250px;
  max-width: 250px;
  height: 98vh;
}

.sidebar-header.open {
  min-width: auto;
  flex-direction: column;
  justify-content: start;
  text-align: start;
  border: #42A5F5, solid 2px;
  border-radius: 5px;
  margin: 1rem 0.5rem 0 0.5rem;
  padding-bottom: 0.5rem;
}

.sidebar-header {
  min-width: auto;
  justify-content: center;
  text-align: start;
  border-bottom: #e7e7e7, solid 2px;
  margin: 1rem 0.5rem 0 0.5rem;
  padding-bottom: 0.5rem;
}

.sidebar-logo {
  margin: 3px 0 0 5px;
  flex-grow: 1;
}

.sidebar-nav {
  padding: 0.5rem 0;
  flex: 1 1 auto;
}

.toggle-btn {
  border-radius: 5px;
  background-color: transparent;
  cursor: pointer;
  border: 0;
  padding: 0.2rem;
  transition: background-color 0.3s ease;
}
.toggle-btn:hover {
  background-color: rgb(114.4371859296, 188.0653266332, 247.5628140704);
}

.toggle-btn img {
  max-width: 40px;
}

.boton {
  position: relative;
}

.sidebar-logo span {
  font-family: "Exo 2", sans-serif;
  color: #000000;
  font-size: 0.9rem;
  font-weight: 600;
}

.sidebar-logo p {
  margin-bottom: 0;
  font-family: "Exo 2", sans-serif;
  color: rgb(114.4371859296, 188.0653266332, 247.5628140704);
  font-size: 0.9rem;
  font-weight: 600;
}

#sidebar:not(.expand) .boton::after {
  border: #e7e7e7, solid 2px;
  border-width: 0 0.16rem 0.16rem 0;
  content: "";
  display: inline-block;
  padding: 4px;
  position: absolute;
  right: 0.1rem;
  top: 1.2rem;
  transform: rotate(-45deg);
  transition: all 0.3s ease-out;
}

#sidebar.expand .boton::after {
  border: #e7e7e7, solid 2px;
  border-width: 0 0.16rem 0.16rem 0;
  content: "";
  display: inline-block;
  padding: 4px;
  position: absolute;
  right: 0.1rem;
  top: 1.2rem;
  transform: rotate(135deg);
  transition: all 0.3s ease-out;
}

#sidebar:not(.expand) .sidebar-logo,
#sidebar:not(.expand) .sidebar-link span {
  display: none;
}

#sidebar:not(.expand) img,
#sidebar:not(.expand) .toggle-btn {
  max-width: 65px;
  padding: 0.1rem;
  padding-right: 1rem;
}

#sidebar.expand img,
#sidebar.expand .toggle-btn {
  max-width: 65px;
  padding: 0.1rem;
  padding-right: 1.3rem;
}

#sidebar:not(.expand) .sidebar-header {
  max-width: 80px;
  margin: 1rem 0.5rem 0 0.5rem;
}

#sidebar:not(.expand) .toggle-btn {
  padding: 0;
}

#sidebar:not(.expand) .sidebar-item .sidebar-dropdown {
  position: absolute;
  top: 0;
  left: 80px;
  background-color: rgb(103.5, 103.5, 103.5);
  padding: 0;
  min-width: 16rem;
  display: none;
}

#sidebar:not(.expand) .sidebar-item:hover .has-dropdown + .sidebar-dropdown {
  display: block;
  max-height: 50em;
  width: 100%;
  opacity: 1;
}
#sidebar:not(.expand) .sidebar-item:hover .has-dropdown + .sidebar-dropdown span {
  display: inline;
}

a.sidebar-link {
  color: rgb(114.4371859296, 188.0653266332, 247.5628140704);
  padding: 0.5rem 1.3rem;
  font-family: "Exo 2", sans-serif;
  display: block;
  font-size: 0.9rem;
  white-space: nowrap;
  border-left: 3px solid transparent;
}

.sidebar-link span {
  color: #000000;
}

.sidebar-link i {
  font-size: 1.6rem;
  margin-right: 0.25rem;
}

a.sidebar-link:hover,
i.sidebar-link {
  transition: all 0.2s ease-out;
  background-color: #42A5F5;
  border-left: 4px solid #FFFFFF;
  color: #e7e7e7;
}
a.sidebar-link:hover span,
i.sidebar-link span {
  color: #e7e7e7;
}

ul.padre .sidebar-link {
  size: 50%;
}

.sidebar-item {
  position: relative;
}

#sidebar .sidebar-footer:hover .sidebar-link {
  background-color: rgb(255, 136.75, 126);
}

#sidebar.expand .sidebar-link[data-bs-toggle=collapse]::after {
  border: solid;
  border-width: 0 0.075rem 0.075rem 0;
  content: "";
  display: inline-block;
  padding: 2px;
  position: absolute;
  right: 1.5rem;
  top: 1.4rem;
  transform: rotate(-135deg);
  transition: all 0.2s ease-out;
}

#sidebar.expand .sidebar-link[data-bs-toggle=collapse].collapsed::after {
  transform: rotate(45deg);
  transition: all 0.2s ease-out;
}

.padre.show a {
  padding-left: 2rem;
}

.financia-dslc {
  border: solid;
  border-color: #ff4242;
}
.financia-dslc input {
  background-color: #ff4242;
  border-color: #ff4242;
}
.financia-dslc input:hover {
  background-color: #ff4242;
  border-color: #ff4242;
}

.financia-slc {
  border: solid;
  border-color: #79d24d;
}
.financia-slc input {
  background-color: #79d24d;
  border-color: #79d24d;
}
.financia-slc input:hover {
  background-color: #79d24d;
  border-color: #79d24d;
}

.nav {
  text-align: center;
}

.btn-volver {
  background-color: #66BB6A;
  border-color: #66BB6A;
  color: #000000;
  min-width: 120px;
  font-family: "Sour Gummy", sans-serif;
}
.btn-volver:hover {
  background-color: rgb(84.3461538462, 179.1538461538, 88.8076923077);
  border-color: rgb(57.5384615385, 129.4615384615, 60.9230769231);
  color: #FFFFFF;
}

.btn-agregar {
  margin: 0 0 1rem 0;
  color: #000000;
  background-color: #42A5F5;
  font-family: "Sour Gummy", sans-serif;
  border-color: #42A5F5;
}
.btn-agregar:hover {
  background-color: rgb(114.4371859296, 188.0653266332, 247.5628140704);
  border-color: rgb(114.4371859296, 188.0653266332, 247.5628140704);
  color: #e7e7e7;
}

.btn-add {
  background-color: #66BB6A;
  border: #66BB6A, solid 1px;
}
.btn-add:hover {
  background-color: rgb(137.3076923077, 202.6923076923, 140.3846153846);
  border-color: rgb(137.3076923077, 202.6923076923, 140.3846153846);
  color: #e7e7e7;
}

.btn-agregar-sbmt {
  color: #000000;
  min-width: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #42A5F5;
  font-family: "Sour Gummy", sans-serif;
  border-radius: 5px;
}
.btn-agregar-sbmt i {
  margin-left: 0.5rem;
}
.btn-agregar-sbmt input {
  color: #000000;
}
.btn-agregar-sbmt input:hover {
  background-color: rgb(17.5628140704, 141.9346733668, 242.4371859296);
}
.btn-agregar-sbmt:hover {
  background-color: rgb(17.5628140704, 141.9346733668, 242.4371859296);
  color: #FFFFFF;
}

.btn-actualizar {
  background-color: #e6ba2d;
  font-family: "Sour Gummy", sans-serif;
  border-color: #e6ba2d;
  min-width: 8rem;
}
.btn-actualizar:hover {
  background-color: rgb(235.4255319149, 200.9744680851, 90.5744680851);
  border-color: rgb(235.4255319149, 200.9744680851, 90.5744680851);
  color: #e7e7e7;
}

.btn-actualizar:not(.peque) {
  background-color: #e6ba2d;
  font-family: "Sour Gummy", sans-serif;
  border-color: #e6ba2d;
  min-width: 8rem;
  margin: 0 3px;
}
.btn-actualizar:not(.peque):hover {
  background-color: rgb(235.4255319149, 200.9744680851, 90.5744680851);
  border-color: rgb(235.4255319149, 200.9744680851, 90.5744680851);
  color: #e7e7e7;
}

.btn-actualizar.peque {
  min-width: auto;
}
.btn-actualizar.peque input {
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.btn-eliminar.peque {
  height: 100%;
  position: relative;
  padding: 0.8rem;
}
.btn-eliminar.peque input {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.btn-actualizar-sbmt {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e6ba2d;
  font-family: "Sour Gummy", sans-serif;
  border-color: solid, #e6ba2d;
  border-radius: 5px;
}
.btn-actualizar-sbmt i {
  margin-left: 0.5rem;
}
.btn-actualizar-sbmt:hover {
  background-color: rgb(235.4255319149, 200.9744680851, 90.5744680851);
  border-color: solid, rgb(235.4255319149, 200.9744680851, 90.5744680851);
  color: #FFFFFF;
}
.btn-actualizar-sbmt input:hover {
  background-color: rgb(235.4255319149, 200.9744680851, 90.5744680851);
}

.btn-eliminar {
  display: flex;
  max-height: 2.4rem;
  align-items: center;
  background-color: #ff5a4b;
  font-family: "Sour Gummy", sans-serif;
  border-radius: 5px;
}
.btn-eliminar input {
  text-align: left;
  width: auto;
  padding-left: 0.3rem;
}
.btn-eliminar i {
