}
.sidebar-footer span {
  color: rgb(126, 10.5, 0);
}

.tarjeta {
  min-height: 180px;
}

.tooltip {
  background-color: transparent !important;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
  border-radius: 0.5rem;
}

.tooltip-inner {
  background-color: rgb(103.5, 103.5, 103.5) !important;
  border-radius: 0.5rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
}

.header-admin {
  position: relative;
}

.modal-backdrop {
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
}

#programs {
  width: auto;
}

#programas_coordinador.oculto {
  display: none;
}

#programas_coordinador_label.oculto {
  display: none;
}

#programas_coordinador_help.oculto {
  display: none;
}

.logo-login {
  border: #FFA726, solid 1px;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 1rem;
}

.login {
  width: 30%;
  background-color: rgb(103.5, 103.5, 103.5);
  padding: 1rem;
  border-radius: 0.5rem;
}
.login label {
  font-family: "Parkinsans", sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 1rem;
  color: #42A5F5;
}

.titulo-login {
  text-transform: uppercase;
  font-family: "Parkinsans", sans-serif;
  font-size: 2.5rem;
}

.subtitulo-login {
  text-transform: uppercase;
  font-family: "Parkinsans", sans-serif;
  font-size: 2rem;
  color: rgb(235.4255319149, 200.9744680851, 90.5744680851);
  margin-top: 2rem;
}

.footer {
  font-size: 14px;
  color: rgb(103.5, 103.5, 103.5);
  font-family: "Lato", sans-serif;
}

.page-login {
  background-color: #e7e7e7;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}
.page-login .login-container {
  background: #FFFFFF;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
}
.page-login .login-container .login-header {
  text-align: center;
  margin-bottom: 20px;
}
.page-login .login-container .login-header h2 {
  color: #42A5F5;
  font-family: "Rubik", Courier;
  margin-bottom: 5px;
}
.page-login .login-container .login-header p {
  color: rgb(103.5, 103.5, 103.5);
  font-family: "Parkinsans", sans-serif;
  font-size: 16px;
}
.page-login .btn-login {
  background-color: #42A5F5;
  color: #000000;
  font-family: "Sour Gummy", sans-serif;
  border: none;
  padding: 10px;
  font-size: 16px;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-bottom: 0.5rem;
  width: 100%;
}
.page-login .btn-login:hover {
  background-color: rgb(10.5025125628, 114.4773869347, 198.4974874372);
}

.password-space {
  position: relative;
}
.password-space button {
  position: absolute;
  top: 0;
  right: 0;
  border: none;
  height: 100%;
  background-color: transparent;
}

.page-change-password {
  background-color: #e7e7e7;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.page-change-password main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}
.page-change-password main .login-container {
  background: #FFFFFF;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 80%;
  max-width: 500px;
}
.page-change-password main .login-container .titulo-login {
  color: #42A5F5;
  font-family: "Rubik", Courier;
  margin-bottom: 20px;
}
.page-change-password main .login-container .texto-login,
.page-change-password main .login-container .texto-advertencia {
  color: rgb(103.5, 103.5, 103.5);
  font-family: "Parkinsans", sans-serif;
  font-size: 16px;
  margin-bottom: 20px;
}
.page-change-password main .login-container .form-control {
  border: 1px solid rgb(103.5, 103.5, 103.5);
  padding: 10px;
  font-family: "Lato", sans-serif;
  border-radius: 8px;
}
.page-change-password main .login-container .form-control::placeholder {
  color: rgb(103.5, 103.5, 103.5);
}
.page-change-password main .login-container .btn-login {
  background-color: #42A5F5;
  color: #000000;
  font-family: "Sour Gummy", sans-serif;
  border: none;
  padding: 10px;
  font-size: 16px;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-bottom: 1rem;
}
.page-change-password main .login-container .btn-login:hover {
  background-color: rgb(10.5025125628, 114.4773869347, 198.4974874372);
}